
@import url(../../common/css/font-face.css);
#header-wrap{box-sizing:border-box;width:100%;background-image:url(../../module/index/images/logo.png);background-color:#FE5400;background-repeat:no-repeat;padding-top:6px;padding-right:0px;padding-bottom:6px;padding-left:37px;padding-right:0.2rem !important;background-size:27px 20px;background-position:4.5px center
}
[data-dpr="2"] #header-wrap{padding-top:12px;padding-right:0px;padding-bottom:12px;padding-left:74px
}
[data-dpr="3"] #header-wrap{padding-top:18px;padding-right:0px;padding-bottom:18px;padding-left:111px
}
[data-dpr="2"] #header-wrap{background-size:54px 40px;background-position:9px center
}
[data-dpr="3"] #header-wrap{background-size:81px 60px;background-position:13.5px center
}
#search{box-sizing:border-box;background-color:#B52600;text-align:center;width:100%;border-radius:4px
}
[data-dpr="2"] #search{border-radius:8px
}
[data-dpr="3"] #search{border-radius:12px
}
#search>span{font-size:13px;line-height:25px;height:25px;color:#F0E0DC
}
[data-dpr="2"] #search>span{font-size:26px
}
[data-dpr="3"] #search>span{font-size:39px
}
[data-dpr="2"] #search>span{line-height:50px
}
[data-dpr="3"] #search>span{line-height:75px
}
[data-dpr="2"] #search>span{height:50px
}
[data-dpr="3"] #search>span{height:75px
}
#search>span:before{font-family:'h5index-iconfont';font-style:normal;-webkit-font-smoothing:antialiased;-webkit-text-stroke-width:0.2px;-moz-osx-font-smoothing:grayscale;display:inline-block;color:#FFF;content:'\E600';font-size:15px;margin-right:5px
}
[data-dpr="2"] #search>span:before{font-size:30px;margin-right:10px
}
[data-dpr="3"] #search>span:before{font-size:45px;margin-right:15px
}



